.loader {
    border: 6px solid red;
    border-radius: 28px;
    left: 49%;
    margin: 34px 76px 0 129px;
    opacity: 0;
    /*position: absolute;*/
    /*top: 50%;*/
    width: 50px;

    animation: pulsate 1s ease-out;
    animation-iteration-count: infinite;
}

@keyframes pulsate {
    0% {
        transform: scale(.1);
        opacity: 0.0;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: scale(1.2);
        opacity: 0;
    }
}